










































/* Styles from Chrome devtools */
kbd {
  display: inline-block;
  vertical-align: middle;
  border: 1px solid #ccc;
  margin: 0 0.1em;
  padding: 0.1em 0.6em;
  color: #333;
  -webkit-box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), 0 0 0 2px #ffffff inset;
          box-shadow: 0 1px 0 rgba(0, 0, 0, 0.2), 0 0 0 2px #ffffff inset;
  border-radius: 3px;
  background-color: #f7f7f7;
  text-shadow: 0 1px 0 #fff;
  line-height: 1;
  font-size: 11px;
  white-space: nowrap;
}
.shortcut-list {
  display: grid;
  grid-template-columns: repeat(2, minmax(auto, -webkit-max-content));
  grid-template-columns: repeat(2, minmax(auto, max-content));
}
.shortcut-item__triggers {
  grid-column: 1;
  justify-self: flex-end;
}
.shortcut-item__description {
  grid-column: 2;
  margin-top: 1px;
  margin-left: 8px;
}



