
.image-viewer {
  position: relative;
  min-height: 100vh;
  background-color: rgb(240, 240, 240);
}
.layout-centered .image-viewer__images {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.image-viewer__img {
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}
.nav-button {
  position: fixed;
  top: 50%;
  display: block;
  border: 0;
  margin-top: -30px;
  padding: 0;
  width: 60px;
  height: 60px;
  cursor: pointer;
  background-color: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  -webkit-transition: 200ms ease-out;
  transition: 200ms ease-out;
  will-change: transform;
}
.nav-button--prev {
  left: 12px;
}
.nav-button--next {
  right: 12px;
}
.nav-button:hover {
  background-color: rgba(0, 0, 0, 0.3);
}
.nav-button svg {
  display: block;
  margin-left: 11px;
  width: 40px;
  height: 40px;
  fill: white;
  -webkit-transition: 200ms ease-out;
  transition: 200ms ease-out;
}
.nav-button:hover svg {
  -webkit-transform: scale(1.1);
          transform: scale(1.1);
}
.nav-button--prev svg {
  margin-left: 8px;
}



