
.help-menu {
  position: fixed;
  z-index: 1;
  top: 12px;
  right: 12px;
  width: 30px;
  height: 30px;
}
.help-menu__toggle {
  width: inherit;
  height: inherit;
  border: 0;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
  -webkit-transition: opacity 200ms ease-out;
  transition: opacity 200ms ease-out;
}
.help-menu__toggle svg {
  display: block;
  width: inherit;
  height: inherit;
  fill: white;
  -webkit-filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.3));
          filter: drop-shadow(0px 0px 2px rgba(0, 0, 0, 0.3));
}
#help-dialog-title {
  margin: 0;
}
h3 {
  margin-top: 0;
}
.odo-dialog__inner {
  border-radius: 12px;
  overflow: hidden;
}
.odo-dialog__close {
  top: 12px;
  right: 12px;
  color: rgb(64, 64, 64);
}
.help-dialog__header {
  padding: 12px;
  background-color: white;
}
.help-dialog__content {
  padding: 12px;
  background-color: rgb(247, 247, 247);
}



